
html,
body,
#root {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  font-family: sans-serif;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.App {
  flex: 1;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

.canvas-container {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 8px;
  box-sizing: border-box;
  background: repeating-conic-gradient(#ccc 0% 25%, #eee 0% 50%) 50% / 20px 20px;
  overflow: auto;
  min-height: 0;
  width: 100%;
}

canvas {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  background-color: transparent;
  border: 1px solid #999;
  display: block;
}

.status-bar {
  height: 40px;
  padding: 0 16px;
  line-height: 40px;
  font-size: 14px;
  text-align: center;
  background-color: #f8f8f8;
  border-top: 1px solid #ccc;
  flex-shrink: 0;
  width: 100%;
  box-sizing: border-box;
}

.controls {
  margin: auto;
}
h1 {
  margin: 20px;
}

input[type="file"] {
  display: none;
}

.controls label {
  display: inline-block;
  background-color: #000000;
  color: white;
  padding: 8px 15px;
  border-radius: 4px;
  cursor: pointer;
}
.over {
  display: flex;
  justify-content: space-between;
}
